import * as LibGenerateTestUserSig from './lib-generate-test-usersig.min.js'

/**
 *  SDKAppId SDKAppId
 *
 * [](https://console.cloud.tencent.com/rav )  SDKAppId
 * 
 */
// const SDKAPPID = 1400618136

/**
 * 
 * <p>
 * 
 * 7 x 24 x 60 x 60 = 604800 = 7 
 */
const EXPIRETIME = 604800

/**
 * 
 *
 * step1. [](https://console.cloud.tencent.com/rav )
 * step2. 
 * step3.  UserSig 
 *
 * Demo UserSig 
 * https://cloud.tencent.com/document/product/647/17275#Server
 */
// const SECRETKEY = 'dcb545159f7fe7541bda0bf47f736d71d5b8436eaef1681d3043f87acbd05e13'

/*
 * Module:   GenerateTestUserSig
 *
 * Function:  UserSigUserSig 
 *            SDKAppIDUserID  EXPIRETIME  HMAC-SHA256
 *
 * Attention:  App 
 *
 *             UserSig SDK 
 *             SECRETKEY  Web 
 *             UserSig 
 *
 *             UserSig  App  UserSig
 *             App
 *
 * Referencehttps://cloud.tencent.com/document/product/647/17275#Server
 */
export function genTestUserSig (SDKAPPID:number, SECRETKEY:string, userID:string): {
  sdkAppID: number,
  userSig: string
} {
  const generator = new LibGenerateTestUserSig(SDKAPPID, SECRETKEY, EXPIRETIME)
  const userSig = generator.genTestUserSig(userID)

  return {
    sdkAppID: SDKAPPID,
    userSig: userSig
  }
}
